/*
  This boilerplate is based in ITCSS and SMACSS
*/
@import url("https://fonts.googleapis.com/css?family=Roboto:300,400");
* {
  box-sizing: border-box;
  margin: 0;
}

body {
  margin: 0 auto;
  font-family: "Roboto", sans-serif;
}

.container {
  padding: 5em 2em;
  width: 100%;
  margin: 0 auto;
}

@media only screen and (max-width: 950px) {
  .container {
    padding-top: 80px;
  }
}

.content {
  display: grid;
  grid-template: repeat(12, 1fr);
  max-width: 1080px;
  width: 100%;
  margin: 0 auto;
  justify-content: center;
}

.content__medium-width {
  max-width: 570px;
}

.flex-col-cc {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.flex-col-ss {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.main__image {
  position: relative;
  text-align: center;
  width: 100%;
}

.float {
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
}

.main__image__img {
  width: 100%;
}

@media only screen and (max-width: 950px) {
  .main__image__img {
    width: 95%;
  }
}

.smartphone {
  width: 420px;
}

@media only screen and (max-width: 950px) {
  .smartphone {
    width: 150px;
  }
}

.text-center {
  text-align: center;
}

.grid-2-col {
  display: grid;
  grid-template: auto/1fr 1fr;
  grid-gap: 30px;
}

@media only screen and (max-width: 950px) {
  .grid-2-col {
    grid-template-columns: 1fr;
  }
}

.grid-3-col {
  display: grid;
  grid-template: 1fr/1fr 1fr 1fr;
  grid-gap: 10px;
}

@media only screen and (max-width: 950px) {
  .grid-3-col {
    grid-template-columns: 1fr;
  }
}

.grid-4-col {
  display: grid;
  grid-template: 1fr/repeat(4, 1fr);
  grid-gap: 20px;
  padding: 2em 0;
}

.grid-4-col img {
  width: 100%;
}

@media only screen and (max-width: 950px) {
  .grid-4-col {
    grid-template-columns: 1fr 1fr;
  }
}

.no-gap {
  grid-gap: 0px;
}

.box img {
  width: 100%;
  padding-bottom: 16px;
}

.box pre {
  padding-bottom: 16px;
}

.box h5 {
  padding-bottom: 16px;
}

.box img, .box pre, .box p, .box h5 {
  padding-left: 17px;
}

@media only screen and (max-width: 950px) {
  .box {
    margin-bottom: 1em;
  }
}

.clients__container {
  background-color: #ECECEC;
  padding: 3em 0;
}

@media only screen and (max-width: 950px) {
  .clients__container {
    padding: 3em 1em;
  }
}

.price__container {
  background-image: url("../img/bg-price.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.price__title {
  color: #ffffff;
}

.card {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 90px 150px 200px 30px;
  grid-row-gap: 10px;
  justify-content: stretch;
  border-radius: 4px;
  max-width: 380px;
  padding: 2em;
  margin: 1em;
}

.card__content {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

@media only screen and (max-width: 950px) {
  .card__content {
    flex-direction: column;
  }
}

@media only screen and (max-width: 950px) {
  .card {
    grid-template-rows: 90px 150px 150px 30px;
  }
}

.card__title, .card__price, .card__features {
  text-align: center;
}

.card__price {
  position: relative;
}

.card__price:before {
  content: "$";
  position: absolute;
  top: 28%;
  right: 190px;
}

.card__features ul {
  padding: 0;
}

.card__features ul li {
  list-style: none;
  padding-bottom: 12px;
}

.card__action {
  margin: 0 auto;
}

.card--normal {
  background-color: #ffffff;
  color: black;
}

.card--normal__price {
  color: #d32f2f;
}

.card--premium {
  background-color: #d32f2f;
  color: white;
}

.bg-color-secondary {
  background-color: #1f1f1f;
  color: white;
}

.bg-color-secondary__transparent {
  background-color: rgba(31, 31, 31, 0.8);
}

.bg-color-white {
  background-color: white;
  color: #1f1f1f;
}

.bg-color-primary {
  background-color: #d32f2f;
}

.contact__img {
  margin: 0 auto;
}

.main__container {
  padding-top: 100px;
}

.main__title {
  padding-bottom: 3em;
  color: #d32f2f;
}

@media only screen and (max-width: 950px) {
  .main__title {
    padding-bottom: 1em;
  }
}

.main__info {
  padding: 4em 2em;
  color: white;
}

.main__info h5 {
  padding-right: 5em;
  padding-bottom: 1em;
}

@media only screen and (max-width: 950px) {
  .main__info {
    padding: 2em 1em;
  }
  .main__info h5 {
    padding-right: 2em;
  }
}

.nosotros__video {
  text-align: center;
  width: 100%;
}

@media only screen and (max-width: 950px) {
  .nosotros__video {
    width: 90%;
  }
  .nosotros__video iframe {
    width: 100%;
    height: 190px;
  }
}

.footer__container {
  padding: 60px 1em 60px 1em;
  background-color: #d32f2f;
}

@media only screen and (max-width: 950px) {
  .footer__content {
    padding: 1em;
  }
}

.footer__logo {
  padding-bottom: 1em;
}

.footer__action {
  padding-bottom: 1em;
}

.footer__rrss {
  padding-bottom: 1em;
}

.footer__rrss a {
  padding-left: 10px;
}

.footer__rrss img {
  transition: transform .2s ease;
}

.footer__rrss img:hover {
  transform: scale(1.2, 1.2);
}

.footer__link {
  margin-right: 14px;
}

@media only screen and (max-width: 950px) {
  .footer__link {
    margin-bottom: 1em;
  }
}

.footer__box {
  color: #ffffff;
}

.footer__box--two {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
}

@media only screen and (max-width: 950px) {
  .footer__box--two {
    align-items: flex-start;
  }
}

.footer__contact {
  color: #ffffff;
  text-align: end;
}

.footer__contact a {
  text-decoration: none;
  color: #ffffff;
}

@media only screen and (max-width: 950px) {
  .footer__contact {
    text-align: start;
  }
}

@keyframes mdc-ripple-fg-radius-in {
  from {
    animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transform: translate(var(--mdc-ripple-fg-translate-start, 0)) scale(1);
  }
  to {
    transform: translate(var(--mdc-ripple-fg-translate-end, 0)) scale(var(--mdc-ripple-fg-scale, 1));
  }
}

@keyframes mdc-ripple-fg-opacity-in {
  from {
    animation-timing-function: linear;
    opacity: 0;
  }
  to {
    opacity: var(--mdc-ripple-fg-opacity, 0);
  }
}

@keyframes mdc-ripple-fg-opacity-out {
  from {
    animation-timing-function: linear;
    opacity: var(--mdc-ripple-fg-opacity, 0);
  }
  to {
    opacity: 0;
  }
}

.mdc-ripple-surface--test-edge-var-bug {
  --mdc-ripple-surface-test-edge-var: 1px solid #000;
  visibility: hidden;
}

.mdc-ripple-surface--test-edge-var-bug::before {
  border: var(--mdc-ripple-surface-test-edge-var);
}

.mdc-button {
  font-family: Roboto, sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-size: 0.875rem;
  line-height: 2.25rem;
  font-weight: 500;
  letter-spacing: 0.08929em;
  text-decoration: none;
  text-transform: uppercase;
  --mdc-ripple-fg-size: 0;
  --mdc-ripple-left: 0;
  --mdc-ripple-top: 0;
  --mdc-ripple-fg-scale: 1;
  --mdc-ripple-fg-translate-end: 0;
  --mdc-ripple-fg-translate-start: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  will-change: transform, opacity;
  padding: 0 8px 0 8px;
  display: inline-flex;
  position: relative;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  min-width: 64px;
  height: 36px;
  border: none;
  outline: none;
  /* @alternate */
  line-height: inherit;
  user-select: none;
  -webkit-appearance: none;
  overflow: hidden;
  vertical-align: middle;
  border-radius: 4px;
}

.mdc-button::before, .mdc-button::after {
  position: absolute;
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
  content: "";
}

.mdc-button::before {
  transition: opacity 15ms linear, background-color 15ms linear;
  z-index: 1;
}

.mdc-button.mdc-ripple-upgraded::before {
  transform: scale(var(--mdc-ripple-fg-scale, 1));
}

.mdc-button.mdc-ripple-upgraded::after {
  top: 0;
  /* @noflip */
  left: 0;
  transform: scale(0);
  transform-origin: center center;
}

.mdc-button.mdc-ripple-upgraded--unbounded::after {
  top: var(--mdc-ripple-top, 0);
  /* @noflip */
  left: var(--mdc-ripple-left, 0);
}

.mdc-button.mdc-ripple-upgraded--foreground-activation::after {
  animation: 225ms mdc-ripple-fg-radius-in forwards, 75ms mdc-ripple-fg-opacity-in forwards;
}

.mdc-button.mdc-ripple-upgraded--foreground-deactivation::after {
  animation: 150ms mdc-ripple-fg-opacity-out;
  transform: translate(var(--mdc-ripple-fg-translate-end, 0)) scale(var(--mdc-ripple-fg-scale, 1));
}

.mdc-button::before, .mdc-button::after {
  top: calc(50% - 100%);
  /* @noflip */
  left: calc(50% - 100%);
  width: 200%;
  height: 200%;
}

.mdc-button.mdc-ripple-upgraded::after {
  width: var(--mdc-ripple-fg-size, 100%);
  height: var(--mdc-ripple-fg-size, 100%);
}

.mdc-button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.mdc-button:active {
  outline: none;
}

.mdc-button:hover {
  cursor: pointer;
}

.mdc-button:disabled {
  background-color: transparent;
  color: rgba(0, 0, 0, 0.37);
  cursor: default;
  pointer-events: none;
}

.mdc-button.mdc-button--dense {
  border-radius: 4px;
}

.mdc-button:not(:disabled) {
  background-color: transparent;
}

.mdc-button:not(:disabled) {
  color: #6200ee;
  /* @alternate */
  color: var(--mdc-theme-primary, #6200ee);
}

.mdc-button::before, .mdc-button::after {
  background-color: #6200ee;
}

@supports not (-ms-ime-align: auto) {
  .mdc-button::before, .mdc-button::after {
    /* @alternate */
    background-color: var(--mdc-theme-primary, #6200ee);
  }
}

.mdc-button:hover::before {
  opacity: 0.04;
}

.mdc-button:not(.mdc-ripple-upgraded):focus::before, .mdc-button.mdc-ripple-upgraded--background-focused::before {
  transition-duration: 75ms;
  opacity: 0.12;
}

.mdc-button:not(.mdc-ripple-upgraded)::after {
  transition: opacity 150ms linear;
}

.mdc-button:not(.mdc-ripple-upgraded):active::after {
  transition-duration: 75ms;
  opacity: 0.16;
}

.mdc-button.mdc-ripple-upgraded {
  --mdc-ripple-fg-opacity: 0.16;
}

